    overflow: hidden;
}
.canvas-box {
    height: 100%;
    width: 100%;
    position: relative;
    display: flex;
}
.pointer-tag {
    display: flex;
    position: absolute;
    top: 30px;
    left: 30px;
    z-index: 1;
    color: #ffffff;
    user-select: none;
    -moz-user-select: -moz-none;
    -webkit-user-select: none;
    -ms-user-select: none;
}
.panels-container {
    position: absolute;
    top: 20px;
    right: 20px;
    width: min-content;
    height: min-content;
    max-height: calc(100% - 40px);
    overflow: hidden;
    overflow-y: scroll;
    transition: 200ms;
}
.panels-container-mobile {
    width: calc(100% - 40px);
}
.panels-container::-webkit-scrollbar {
    background: #aaa;
    width: 5px;
}
.panels-container::-webkit-scrollbar-thumb
{
    background-color:#333;
}
.panels-wrapper {
    width: 360px;
}
.panels-container-mobile >>> .panels-wrapper {
    width: 100%;
}
